<template>
<div>

    <el-menu theme="dark" default-active="1" class="el-menu-demo" mode="horizontal" @select="handleselect">
      <el-menu-item index="1"></el-menu-item>
      <el-submenu index="2">
        <template slot="title"></template>
          <el-menu-item index="2-1" >1</el-menu-item>
          <el-menu-item index="2-2">2</el-menu-item>
          <el-menu-item index="2-3">3</el-menu-item>
      </el-submenu>
      <el-menu-item index="3"></el-menu-item>
    </el-menu>


<el-form ref="form" :model="form" label-width="80px" @submit.prevent="onSubmit">
  <el-form-item label="">
    <el-input v-model="form.name"></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-select v-model="form.region" placeholder="">
      <el-option label="" value="shanghai"></el-option>
      <el-option label="" value="beijing"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="">
    <el-col :span="11">
      <el-date-picker type="date" placeholder="" v-model="form.date1" style="width: 100%;"></el-date-picker>
    </el-col>
    <el-col class="line" :span="2">-</el-col>
    <el-col :span="11">
      <el-time-picker type="fixed-time" placeholder="" v-model="form.date2" style="width: 100%;"></el-time-picker>
    </el-col>
  </el-form-item>
  <el-form-item label="">
    <el-switch on-text="" off-text="" v-model="form.delivery"></el-switch>
  </el-form-item>
  <el-form-item label="">
    <el-checkbox-group v-model="form.type">
      <el-checkbox label="/" name="type"></el-checkbox>
      <el-checkbox label="" name="type"></el-checkbox>
      <el-checkbox label="" name="type"></el-checkbox>
      <el-checkbox label="" name="type"></el-checkbox>
    </el-checkbox-group>
  </el-form-item>
  <el-form-item label="">
    <el-radio-group v-model="form.resource">
      <el-radio label=""></el-radio>
      <el-radio label=""></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="">
    <el-input type="textarea" v-model="form.desc"></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-input-number v-model="num1" @change="handleChange"></el-input-number>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click.native.prevent="onSubmit"></el-button>
    <el-button @click.native.prevent></el-button>
  </el-form-item>
</el-form>

      <el-checkbox-group v-model="checkList">
        <el-checkbox class="checkbox" label=" A"></el-checkbox>
        <el-checkbox class="checkbox" label=" B"></el-checkbox>
        <el-checkbox class="checkbox" label=" C"></el-checkbox>
        <el-checkbox class="checkbox" label="" disabled></el-checkbox>
        <el-checkbox class="checkbox" label="" disabled></el-checkbox>
      </el-checkbox-group>

    <el-time-select
      v-model="value_time"
      :picker-options="{
        start: '08:30',
        step: '00:15',
        end: '18:30'
      }"
      placeholder="">
    </el-time-select>

    <el-switch
      v-model="value1"
      on-text=""
      off-text="">
    </el-switch>
    <el-switch
      v-model="value2"
      on-color="#13ce66"
      off-color="#ff4949">
    </el-switch>


<!-- Table -->
<el-button type="text" @click.native="dialogTableVisible = true"> Dialog</el-button>

<el-dialog title="" v-model="dialogTableVisible">
  <el-table
    :data="tableData"
    selection-mode="multiple"
    style="width: 100%"
    @selectionchange="">
    <el-table-column
      type="selection"
      width="50">
    </el-table-column>
    <el-table-column
      inline-template
      label=""
      width="120">
      <div>{{ row.date }}</div>
    </el-table-column>
    <el-table-column
      property="name"
      label=""
      width="120">
    </el-table-column>
    <el-table-column
      property="address"
      label=""
      show-tooltip-when-overflow>
    </el-table-column>
  </el-table>
</el-dialog>




  </div>
</template>
<script>
  export default {
    data() {
      return {
          num1: 1,
          tableData: [{
            date: '2016-05-02',
            name: '',
            address: ' 1518 '
          }, {
            date: '2016-05-04',
            name: '',
            address: ' 1517 '
          }, {
            date: '2016-05-01',
            name: '',
            address: ' 1519 '
          }, {
            date: '2016-05-03',
            name: '',
            address: ' 1516 '
          }],
        dialogTableVisible: false,
        value1: true,
        value2: false,
        checkList: ['',' A'],
        value_time: new Date(),
        form: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        }
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
        console.log(this.form.name)
      },
      handleselect() {
        console.log("handleselect");
      },
      handleChange () {
        console.log(this.num1);
        // this.num ++;
      }
    }
  }
</script>


